import React from 'react';
import data from './data';
import './App.css';

interface ItemProps {
  index: number;
  item: any;
}

const WINDOW_HEIGHT = window.innerHeight;

class Item extends React.Component<ItemProps> {
  _ready = (this.props.index - 1) * WINDOW_HEIGHT;
  _start = this.props.index * WINDOW_HEIGHT;
  _end = (this.props.index + 1) * WINDOW_HEIGHT;
  _content = React.createRef<HTMLDivElement>();

  componentDidMount() {
    window.addEventListener('scroll', this.onScroll);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll);
  }
  onScroll = () => {
    const { scrollTop } = document.documentElement;
    const { _ready, _start, _end } = this;
    if (scrollTop >= _ready && scrollTop <= _end && this._content.current) {
      const percent = (scrollTop - _start) / WINDOW_HEIGHT;
      this._content.current.setAttribute(
        'style',
        `transform: translate(0, ${(scrollTop - _start) / 2}px);
        opacity: ${1 - Math.abs(percent)};`
      );
    }
  };
  render() {
    const { index, item } = this.props;
    return (
      <div className="item" key={index} style={{ height: WINDOW_HEIGHT }}>
        <div style={{ backgroundImage: `url(${item.imageUrl})` }} className="cover" />
        <div className="content" ref={this._content}>
          <div className="title">{item.title}</div>
          <div className="excerpt">{item.excerpt}</div>
        </div>
      </div>
    );
  }
}

export default class App extends React.Component {
  render() {
    return (
      <div>
        {data.map((item, index) => (
          <Item index={index} item={item} key={index} />
        ))}
      </div>
    );
  }
}
